*{
    margin: 0;
    padding: 0;
}

body{
    background-color: aqua;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* height: 100vh; */
    overflow: auto;
}
form
{
    background-color: palevioletred;
    padding: 20px;
    border-radius: 5px;
    width:30% ;
    margin-bottom: 20px;
    margin-left: auto;
    margin-right: auto;

}

form label{
    font-size: 15px;
    font-weight: bold;
}

input{
    background-color: aquamarine;
    border: none;
    width: 100%;
    padding: 12px 0;
    border-radius: 4px;
    margin-top: 12px;
    margin-bottom: 12px;
    font-size: 15px;
}

.btn{
    background-color: rgb(127, 146, 255);
    padding: 10px;
    width: 50%;
    margin-left: 25%;
    font-size: 20px;
    border: black 1px solid;
    font-weight: bold;
    cursor: pointer;
}